import React, {useEffect} from 'react';
import "../index.css";
import "./Skills.css";

//Skill Set List
import SkillList from './SkillList';
import {motion, useAnimation} from 'framer-motion';
import {useInView} from 'react-intersection-observer';


function Skills()
{
    const Canimation = useAnimation();
    const {ref, inView} = useInView({threshold: 0.3});

    useEffect(() => {
        if(inView)
        {
            Canimation.start({
                opacity: 1,
                transition: {duration: 1}
            })
        }

        else if(!inView)
        {
            Canimation.start({
                opacity: 0,
                transition: {duration: 2}
            })
        }
    }, [inView])
    return(
        <section ref = {ref} name = "Skill-Set" className = "Skills-Section">
            <div className = "Skills-Content">
                <h1 className = "Heading-Font text-center">[ SKILL - SET ]</h1>
                
                <div className = "Skill-ShowCase row mt-3">
                    
                    {SkillList.map( n => {

                        const {id, img_url, name, percentage, bg, projects, certi} = n;

                        return(
                            <motion.div animate={Canimation} key = {id} className = "Skill-Box shadow col-md">
                                <img src = {img_url} alt= "image" className = "img-fluid Skill-Image" />
                                <p className = "Main-Font Skill-Name">{name}</p>
                                <div className="progress" style={{height: '20px'}}>
                                    <div className={"progress-bar", bg} role="progressbar" style={{width: percentage}} aria-valuemin="0" aria-valuemax="100"></div>
                                </div>

                                <div className = "Skill-Details My-column">
                                    <p className = "Sub-Font"><b>Projects: {projects}</b></p>
                                    {certi? <p className = "text-primary"><i className ="fas fa-check text-success"></i> CERTIFIED</p>: <p></p>}
                                </div>
                            </motion.div>
                        )

                    })}
                </div>
            </div>
        </section>
    );
}

export default Skills;