import React, { useState, useEffect } from 'react';
import "./Navbar.css";
import "../index.css";
import * as Scroll from 'react-scroll';

// Or Access Link,Element,etc as follows
let Link      = Scroll.Link;
// let Button    = Scroll.Button;
// let Element   = Scroll.Element;
// let Events    = Scroll.Events;
// let scroll    = Scroll.animateScroll;
// let scrollSpy = Scroll.scrollSpy;


function Navbar()
{
    const [nav, setNav] = useState(false);

    function ChangeNavColor()
    {
        if(window.scrollY >= 140)
        {
            setNav(true);
        }

        else{
            setNav(false);
        }
    }

    useEffect(() => {
        window.addEventListener("scroll", ChangeNavColor);

        return () => {
            window.removeEventListener("scroll", ChangeNavColor);
        }
    })

    return(
        <header className = "sticky-top">
            <nav className = { nav ? "navbar navbar-expand-lg navbar-light bg-light shadow" : "navbar navbar-expand-lg navbar-dark bg-transperant"}>
                <div className ="container-fluid">
                    <span className ="navbar-brand Main-Font">KEERTHI VIBISAN</span>
                    <button className ="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarNav" aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation">
                    <span className ="navbar-toggler-icon"></span>
                    </button>
                    <div className ="collapse navbar-collapse" id="navbarNav">
                    <ul className ="navbar-nav ms-auto Main-Font">

                        <li className = "nav-item">
                            <Link activeClass = "active" spy = {true} exact = 'true' smooth = {true} duration={400} to = "Home" className = "nav-link" >Home</Link>
                        </li>

                        <li className = "nav-item">
                            <Link activeClass = "active" spy = {true} exact = 'true' smooth = {true} duration={400} to = "Works" className = "nav-link">Works</Link>
                        </li>

                        <li className = "nav-item">
                            <Link to = "Skill-Set" className = "nav-link" activeClass = "active" spy = {true} smooth = {true} duration={400} exact = 'true'
                            >Skills</Link>
                        </li>

                        <li className = "nav-item">
                            <Link to = "About" className = "nav-link" activeClass = "active" spy = {true} smooth = {true} duration={400} exact = 'true'>About</Link>
                        </li>

                    </ul>
                    </div>
                </div>
            </nav>
        </header>
    )
}

export default Navbar;