import React, { useEffect, useState } from "react";
import "../index.css";
import{Link, useParams} from 'react-router-dom';
import ProjectList from "./ProjectList.js";


function Desc()
{

    const{id} = useParams();
    const [ids, setId] = useState();
    const [name, setName] = useState("");
    const [Bg, setBg] = useState("");
    const [intro, setIntro] = useState("");
    const [video, setVideo] = useState("");
    const [about, setAbout] = useState("");
    const [git, setGit] = useState("");
    const [src, setSrc] = useState();
    const [contrib, setContrib] = useState("");
    const[live, setLive] = useState("");


    useEffect(() => {
        //We get all Details need to Display
        const all = ProjectList.find( n => n.id === parseInt(id))
        setId(all.id);
        setName(all.name);
        setBg(all.BGcolor);
        setIntro(all.intro);
        setVideo(all.video);
        setAbout(all.about);
        setGit(all.GitHub);
        setContrib(all.Contribs);
        setLive(all.liveLink);
        // const res = videoFixer();
    }, [])

    return(
    <section key = {ids} className = "Section" style = {{backgroundColor: Bg}}>
        <nav className = "NAV">
            <p className = "Main-Font">KEERTHI VIBISAN</p>
            <Link to = "/" className = "BACK">[ BACK ]</Link>
        </nav>
        <div className = "Desc-Container My-column">
            <h1 className = "Main-Font Desc-Heading">{name}</h1>
            <p className = "Sub-Font Desc-Writings mt-4">{intro}</p>

            <div className = "My-row">

                <video autoPlay loop muted className = "Video">
                    <source src = {video} type = "video/mp4"></source>
                </video>
                
                
             </div>

             <p className = "Sub-Font Desc-Writings mt-5">{about}</p>

             <a href = {live} className = "Sub-Font Project-Links" rel="noreferrer" target = "_blank"><i class="fas fa-link"></i> Website is Live</a>
             <a href = {git} className = "Sub-Font Project-Links" rel="noreferrer" target = "_blank"><i className ="fab fa-github text-white"></i> GitHub</a>
             <a href = {contrib} className = "Sub-Font Project-Links" rel="noreferrer" target = "_blank"><i className ="fab fa-github text-white"></i> My Contributions</a>

         </div>
         <div className = "My-column pb-5 pt-5">
             <Link to = "/" className = "Heading-Font BACK">[ BACK ]</Link>
         </div>
         <hr style = {{height: '3px', color: "black"}} />

         <div className = "text-center">
             <p className = "Main-Font Footer-Writes">Reach Me 📧</p>
             <a href = "mailto:keerthivibisan.ct20@bitsathy.ac.in"rel="noreferrer" target = "_blank" className = "Main-Font FooterEmail">keerthivibisan.ct20@bitsathy.ac.in</a>
         </div>
     </section>
     );
}

export default Desc;