import React, { useState } from 'react';
import SList from './SummaryList';

export default function Summary()
{
    const[index, setIndex] = useState(0);

    let {name, desc, desc2} = SList[index];

    return(
        <section className = "Summary-section">

            <p className = "Heading-Font text-center media-heading">[ Qualification's ]</p>

            <div className = "Summary-Container">
                <div className = "button-container">
                    {SList.map( n => {
                        
                        const{id, name} = n;

                        return(
                            <button className = {id === index ? "summary-act Main-Font":"summary-btn Main-Font"} key = {id} onClick = {() => setIndex(id)}>{name}</button>
                        )

                    })}
                </div> 

                <div className = "summary-items-container Main-Font column">
                    <h2>{name}</h2>
                    <p>{desc}</p>
                    <p>{desc2}</p>
                </div>
            </div> 
            
        </section>
    )
}