import React, { useEffect, useState } from 'react';
import ReactDom from 'react-dom';
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';
// import "./index.css";
import Navbar from './Navbar/Navbar';
import Home from './Home/Home';
import Works from './Works/Work';
import Summary from './Works/SummaryTabs';
import Skills from './Skill-Set/Skills';
import About from './About/About';
import Footer from './Footer/Footer';
import Desc from './Works/Desc';

function ScrollSpy()
{
  return(
    <>
      <Navbar />
      <Home />
      <Works />
      <Skills />
      <About />
      <Summary />
      <Footer />
    </>
  )
}
function AllWrapper()
{
  const [loading, setLoading] = useState(true);

  useEffect(() =>{
    window.addEventListener('onload', setLoading(false));
  }, [])

  if(loading)
  {
    return(<>Loading...</>)
  }

  else{
    return(
      <Router>

        <Switch>
           <Route path = "/" exact>  
              <ScrollSpy />
          </Route>

          <Route path = "/Details/:id" children = {<Desc />}></Route>
        </Switch>

      </Router>
    );
  }
}

ReactDom.render(<AllWrapper />, document.getElementById("root"));
