import React, {useEffect} from 'react';
import "../index.css";
import "./about.css";

import {motion, useAnimation} from 'framer-motion';
import {useInView} from 'react-intersection-observer';

function About()
{
    const Aanimation = useAnimation();
    const {ref, inView} = useInView();

    useEffect(() => {
        if(inView)
        {
            Aanimation.start({
                y: 0,
                transition: {duration: 1.5}
            })
        }

        else if(!inView)
        {
            Aanimation.start({
            y: "10%",
            transition: {duration: 2}
            })
        }
    }, [inView])
  
    return(
    <motion.section ref = {ref} animate = {Aanimation} name = "About" className = "About-Section">
        <div className = "About-Content">
            {/* <h1 className = "Main-Font ABOUT text-center">[ ABOUT ]</h1> */}
            <div className = "About-Writings">
                <p className = "Main-Font Hero-Intro">HI. IAM KEERTHI VIBISAN. I'M A FULL STACK DEVELOPER.</p>
                <hr style = {{height: "1px"}} className = "mt-5"/>
                <p className = "Sub-Font">Location: Tamilnadu, India</p>
                <br/><br/>
                <p className = "Main-Font About-sub-Text">
                Technophile and able to adapt to new environments.</p> <br/>
                <p className = "Main-Font About-sub-Text">Currently Student at Bannari Amman Institute of Technology. 2024 will be my year of Graduation.</p> <br/><br/>
            </div>
        </div>
    </motion.section>
    );
}

export default About;
