import React, { useEffect } from 'react';

import "../index.css";
import "./Home.css";

import { motion, useAnimation } from 'framer-motion';
import {useInView} from 'react-intersection-observer';
//initial = {{opacity: 0}} animate = {{opacity: 1}} transition = {{duration: 2}}
function Home()
{
    const{ref, inView} = useInView({threshold: 0.2});
    const animation = useAnimation();
    const animation2 = useAnimation();
    useEffect(() => {
        console.log(inView);
        if(inView)
        {
            //console.log("true");
            animation.start({
                scale: 1,
                transition: {
                    duration: 2
                }
            })

            animation2.start({
                y: 0,
                transition: {duration: 1}
            })
        }

        else if(!inView){
            animation.start({
                scale: 0.5
            })

            animation2.start({
                y: '30%'
            })
        }
    }, [inView]);

    return(
        <>
            <section name = "Home" className = "Home-Section My-row text-white">
                <div className = "Content My-column">
                    <div className = "Hero-Content">
                        <div>
                            <motion.p animate = {animation} ref = {ref} className = "Main-Font Side-Text">KEERTHI VIBISAN</motion.p>

                            <div className = "Li-Circle email-li"> 
                                <a href = "mailto:keerthivibisan.ct20@bitsathy.ac.in" target = "_blank"  rel="noreferrer"><i className="text-danger far fa-envelope fa-2x"></i></a>
                            </div>

                            <div className = "Li-Circle Linkedin-li">
                                <a href = "https://linkedin.com/in/keerthi-vibisan-s-052567210" target = "_blank" rel="noreferrer"><i className="text-primary fab fa-linkedin-in fa-2x"></i></a>
                            </div>

                            <div className = "Li-Circle gitHub-li">
                               <a href = "https://github.com/Keerthi-Vibisan-S" target = "_blank" rel = "noreferrer"><i className="text-dark fab fa-github fa-2x"></i></a>
                            </div>
                            
                            <motion.h1 animate = {animation2} className = "My-Name mb-3">FULL Stack Developer</motion.h1>
                        </div>
                    </div>

                </div>
            </section>
        </>
    );
}

export default Home;