import React, { useEffect } from 'react';
import "./Work.css";
import "../index.css";
import {Link} from 'react-router-dom';
import { motion, useAnimation } from 'framer-motion';
import {useInView} from 'react-intersection-observer';

//Project List
import ProjectList from "./ProjectList.js";
//data-aos="zoom-in" data-aos-duration = "2000" data-aos-offset="100"

function Works()
{
    const Panimation = useAnimation();
    const{ref, inView} = useInView({threshold: 0.3});

    useEffect(() => {

        if(inView)
        {
            //console.log("true");
            Panimation.start({
                opacity: 1,
                transition: {
                    duration: 1
                }
            })
        }

        else if(!inView){
            Panimation.start({
                opacity: 0,
                transition: {duration: 2}
            })
        }

    }, [inView])

    return(
        <section ref = {ref} name = "Works" className = "Works-Section">
            <div className = "Works-content">
                <h1 className = "Heading-Font text-center">[ WORKS ]</h1>

                <div className = "Projects-ShowCase">
                    {
                        ProjectList.map( n => {

                            const {id, name, image_url, type, page} = n;

                            return(
                                <motion.div animate = {Panimation} key = {id}><Link to = {page+'/'+id}
                                className = "Project-Box No-Link" 
                                onMouseOver = {(e) => {
                                    let ele = e.currentTarget;
                                    ele.classList.add('hoverEfeect');
                                }}
                                
                                onMouseOut = {(e) => {
                                    let ele = e.currentTarget;
                                    ele.classList.remove('hoverEfeect');
                                }}
                                >
                                        <div className ="img-text-wrapper">
                                            <img src = {image_url} alt = "image" className = "img-fluid Project-image" />
                                            <p className = "Sub-Font Project-Badge">{type}</p>
                                        </div>
                                        <h4 className = "Main-Font Project-Name">{name}</h4>
                                </Link></motion.div>
                            )
                        })
                    }
                </div>
            </div>
        </section>
    );
}

export default Works;